const getData = async (owner: string, repo: string, path: string):Promise<Object | null> => {
    // GitHub API  URL 
    //const apiUrl = `https://api.github.com/repos/${owner}/${repo}/contents/${path}`;
    const _apiUrl = `https://raw.githubusercontent.com/${owner}/${repo}/main/${path}`;
    let result: Object|null = null;
    // GitHub API GET  
    // await fetch(apiUrl)
    // .then(response => response.json())
    // .then(data => {
    //     //   data.content base64   .
    //     const content = encodeURIComponent(atob(data.content));

    //     //     
    //     //console.log(" "+content);
    //     result = JSON.parse(decodeURIComponent(content));
    // })
    // .catch(error => {
    //     console.error(' :', error);
    // });

    await fetch(_apiUrl)
    .then(res => res.json())
    .then(data => {
        //console.log(data);
        result = data;
    })

    // console.log("")
    // console.log(result);
    return result;
}

export default getData;