import styled from "styled-components";

const OnedayClassInfoPage = styled.div`
  .page-container {
    padding: 20px;
    padding-left: 20rem;
    padding-right: 20rem;
    padding-top: 5rem;
    justify-content: center;
    display: flex;
  }

  .class-container {
    height: fit-content;
    width: auto;
    background-color: #252525;
    padding: 80px;
    padding-top: 60px;
  }

  h2 {
    color: white;
    font-size: 2.5rem;
    font-weight: bold;
    padding-bottom: 60px;
    font-family: "AppleSDGothicNeoSB";
  }
  button{
    width: 100%;
    background-color: #4e4e4e;
    color: white;
    padding: 10px 20px;
    font-size: 18px;
    cursor: pointer;
    border-radius: 30px;
    border-color: transparent;
    border: 1.5px solid white;
    transition: background-color 0.3s ease;
    font-family: "AppleSDGothicNeoSB";
  }
  button:hover{
    background-color: #a4a4a4;
  }
  img {
    width: 100%; /*   100%        */
    height: 400px;
    object-fit: cover; /*     */
    object-position: center center; /*       */
    margin-bottom: 2rem;
  }
  p {
    color: white;
    font-size: 1rem;
    font-family: "AppleSDGothicNeoSB";
  }
  
  .text-container{
    margin-top: 2rem;
    h2{
        padding-top: 2rem;
        font-size: 2rem;
        padding-bottom: 2rem;
        font-family: "AppleSDGothicNeoSB";
    }
    p{
        padding: 10px;
        font-family: "AppleSDGothicNeoSB";
    }
  }
`;

export default OnedayClassInfoPage;
