import styled from "styled-components";

const AboutUsPage = styled.header`
/* AboutUs.css */

section{
  opacity: 0.3;
  transform: translate(0, 2rem);
  transition: 1s;
}
.show{
  transform: translate(0, 0);
  opacity: 1;
}

.section1{
  position: relative;
}
.section1 p {
  display: flex;
  position: absolute;
  line-height: 70px;
  top: 20%;
  left: 20%;
  color: white; /*      */
  font-size: 3.5rem;
  font-family: "AppleSDGothicNeoSB";
}
.section1 h1 {
  position: absolute;
  top: 60%;
  left: 20%;
  color: white; /*      */
  font-size: 6rem;
  font-family: "AppleSDGothicNeoSB";
}
.section2 {
  position: relative;
}
.section2 p {
  text-align: center;
  line-height: 100px;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white; /*      */
  font-size: 3rem;
  font-family: "AppleSDGothicNeoSB";
}
.section3 {
  position: relative;
}
.section3 p {
  text-align: right;
  line-height: 100px;
  position: absolute;
  top: 45%;
  right: 30%;
  transform: translate(50%, -50%);
  color: white; /*      */
  font-size: 3rem;
  font-family: "AppleSDGothicNeoSB";
}
.section4 {
  position: relative;
}
.section4 p {
  text-align: center;
  line-height: 100px;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white; /*      */
  text-shadow: 4px 4px 4px black;
  font-size: 3rem;
  font-family: "AppleSDGothicNeoSB";
}
.section5 {
  position: relative;
}
.section5 p {
  text-align: center;
  line-height: 100px;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white; /*      */
  text-shadow: 4px 4px 4px black;
  font-size: 3rem;
  font-family: "AppleSDGothicNeoSB";
}
.section6 {
  position: relative;
}
.section6 p {
  text-align: center;
  line-height: 100px;
  position: absolute;
  top: 33%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white; /*      */
  text-shadow: 2px 4px 2px black;
  font-size: 4rem;
  font-family: "Sinoreta_PERSONAL_USE_ONLY";
}
.section6 span {
  text-align: center;
  line-height: 100px;
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white; /*      */
  text-shadow: 2px 4px 2px black;
  font-size: 1.5rem;
  font-family: "Sinoreta_PERSONAL_USE_ONLY";
}
.section6 h1 {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white; /*      */
  text-shadow: 4px 4px 2px black;
  font-size: 8rem;
  font-family: "Sinoreta_PERSONAL_USE_ONLY";
}



.imgcontainer {
  position: relative;
  width: 100%;
  text-align: center;
}
.imgcontainer::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4); /* 20%    */
}
.imgcontainer img {
  width: 100%;
  height: auto;
  display: block;
  margin: 0 auto;
}
.img3 {
  height: 100vh; /*        */
  width: auto; /*       */
}
`;

export default AboutUsPage;
